@import "App.scss";

.footer {
  width: 100%;
  background-color: #5ab5c59f;
  font-family: $font2;
  color: rgb(49, 49, 49);

  .copyright {
    font-size: 14px;
    transform: translateY(2rem);
    margin-left: 1rem;
  }

  .link-btn {
    position: relative;
    top: -3rem;
    left: 50%;
    .btn-up {
      width: 4rem;
    }
  }
}

.dark {
  background-color: rgb(92, 92, 92);
  color: white;
}
